import HiGerooBtn from "./HiGerooBtn"; // 
const HGR = {
    install(Vue, options) {
        Vue.component(HiGerooBtn.name, HiGerooBtn); // HiGerooBtn.name name
    }
};
if (typeof window !== "undefined" && window.Vue) {
    window.Vue.use(HGR);
}
export default HGR; // ..


